import React from "react";
import { NavLink } from 'react-router-dom'; 

const Navigation = () => {
  return (
    <div>
       <NavLink to="/">
        Home
      </NavLink>
      <NavLink to="/about">
        About
      </NavLink>
      <NavLink to="/experiences">
        Experiences
      </NavLink>
      <NavLink to="/projects">
        Projects
      </NavLink>
    </div>
  );
};

export default Navigation;


